body.lightmode{
	background: #000;
	color: white;
}
body.lightmode .app{
	border: 2px solid white;
}
body.lightmode .alert{
	background: black;
	border: 2px solid white;
	color: white;
	.bLeft{
		background: white;
	}
}
body.lightmode .ripple{
	background: white;
}
body.lightmode button{
	border: 2px solid white;
	color: white;
	&:hover{
		color: black;
	}
}
body.lightmode .info-text{
	background: black;
	border: 2px solid white;
	button{
		border: 2px solid white;
	}
}
body.lightmode .darkmode-icon{
	background: white;
	color: black;
}
body.lightmode .fa-sun{
	display: block;
}
body.lightmode .fa-moon{
	display: none;
}